<template>
  <div class="qin-header">
    <span @click="handleLogout">退出登录</span>
  </div>
</template>

<script>
export default {
  methods: {
    handleLogout() {
      localStorage.setItem('token', '');
      this.$store.commit('SET_TOKEN', '');
      this.$router.push('/login');
    }
  }
};
</script>

